// import {Link} from "gatsby"
import React from "react"
import prof from '../images/IMG_6753.jpg'
import { Link} from "react-scroll";


const Home = () => {
    return (



      <div className="relative z-20 flex items-center justify-center overflow-hidden bg-white dark:bg-gray-900 min-h-3/4 " id="home">
    <div className="container relative flex justify-center px-6">
      
      
      <div className="relative z-20 flex flex-col sm:w-2/3 lg:w-2/5">

            <img alt="ERR" src={prof} className="mx-auto mb-3 rounded-full w-28"/>

         
            <h1 className="mb-8 text-6xl font-black leading-none text-center text-gray-800 uppercase dark:text-white sm:text-8xl">Welcome to my website</h1>

            <p class="test-xm sm:text-xl text-gray-700 dark:text-white text-center">
               Student. Developer. Leader. 
            </p>

            <div className="justify-around hidden mt-8 sm:flex">
              <div></div><div></div>
            <button className="py-3.5 px-10 border-2 border-transparent text-white transition duration-200 rounded shadow-md md:w-auto  md:mb-0 bg-pink-500 hover:bg-indigo-500 focus:shadow-outline focus:outline-none">
            <Link activeClass="active" to="about" spy={true} smooth={true} offset={-200} duration={500}>Learn More</Link>
                </button>

             
                <div></div><div></div>
            </div>

            <div className="flex flex-col justify-around mt-8 sm:hidden">
            <button className="py-3.5 px-5 mb-4 border-2 border-transparent text-white transition duration-200 rounded shadow-md md:w-auto  md:mb-0 bg-pink-500 hover:bg-indigo-500 focus:shadow-outline focus:outline-none">
            <Link activeClass="active" to="projects" spy={true} smooth={true} offset={-200} duration={500}> Learn More </Link>
                </button>

            
            </div>
        </div>
        
    </div>
</div>

    )
}

