import React, { useState } from "react"
import About from "../components/about"
import Contact from "../components/contact"
import Home from "../components/home"
import Navbar from "../components/navbar"
import Skills from "../components/skills"
import Projects from "../components/projects"
import { Helmet } from "react-helmet"
import { Link } from "react-scroll";

const IndexPage = () => {


  const [isDark, setIsDark] = useState(true)

  const buttonHandler = () => {
    setIsDark(current => !current)
  }


  return (
    <div className={isDark ? "dark" : "light"}>

      <Helmet htmlAttributes={{ lang: 'en' }}>
        <meta charSet="utf-8" />
        <meta name="description" content="Soham Manoli an enthusiastic and passionate High School Junior who loves to learn new things in order to make the world a better place!"/>
        <title>Soham.Manoli</title>
      </Helmet>

      <div className="fixed z-50 bottom-10 right-10 ">
        <button
          className={isDark ? "w-12 h-12 p-0 transition duration-200 ease-in bg-pink-500 rounded-full shadow active:shadow-lg mouse focus:outline-none" :
          "w-12 h-12 p-0 transition duration-200 ease-in rounded-full shadow bg-indigo-500 active:shadow-lg mouse focus:outline-none"
          } onClick={buttonHandler}>
          {/* <svg viewBox="0 0 20 20" enable-background="new 0 0 20 20" class="w-6 h-6 inline-block">
            <path fill="#FFFFFF" d="M16,10c0,0.553-0.048,1-0.601,1H11v4.399C11,15.951,10.553,16,10,16c-0.553,0-1-0.049-1-0.601V11H4.601
                                    C4.049,11,4,10.553,4,10c0-0.553,0.049-1,0.601-1H9V4.601C9,4.048,9.447,4,10,4c0.553,0,1,0.048,1,0.601V9h4.399
                                    C15.952,9,16,9.447,16,10z" />
          </svg> */}
          <span className="" aria-label="jsx-a11y/accessible-emoji" role="img"></span>

        </button>
      </div>

      <Navbar></Navbar>
      <Home></Home>
      <About></About>
      <Projects></Projects>
      <Skills></Skills>
      <Contact></Contact>




      <footer className="flex justify-center px-4 text-black bg-white dark:text-white dark:bg-gray-900">
        <div className="container py-6">

          <hr className="h-px mt-6 bg-pink-500 border-none" />

          <div className="flex flex-col items-center justify-between mt-6 md:flex-row">
            <div>
              <h1 className="text-xl font-bold"><span className="text-pink-500">SOHAM</span>.MANOLI</h1>
            </div>
            <div className="mt-3 sm:mt-4">
              Made with <span aria-label="jsx-a11y/accessible-emoji" role="img"></span> by Soham Manoli
                </div>
            <div className="flex mt-4 md:m-0">
              <div className="-mx-4">
                <button className="px-4 text-sm focus:outline-none"><Link activeClass="active" to="about" spy={true} smooth={true} offset={-200} duration={500}>About Me</Link></button>
                <button className="px-4 text-sm focus:outline-none"><Link activeClass="active" to="projects" spy={true} smooth={true} offset={-200} duration={500}>Projects</Link></button>
                <button className="px-4 text-sm focus:outline-none"><Link activeClass="active" to="skills" spy={true} smooth={true} offset={-200} duration={500}>Skills</Link></button>
                <button className="px-4 text-sm focus:outline-none"><Link activeClass="active" to="home" spy={true} smooth={true} offset={-200} duration={500}>Back to Top</Link></button>

              </div>
            </div>
          </div>
        </div>
      </footer>

    </div>
  )
}

/* <div className="mb-3">
            <div className="relative inline-block w-10 mr-2 align-middle select-none">
                <input type="checkbox" name="toggle" id="Pink" className="absolute block w-6 h-6 duration-200 ease-in bg-white border-4 rounded-full outline-none appearance-none cursor-pointer checked:bg-pink-500 focus:outline-none right-4 checked:right-0"/>
                    <label for="Pink" className="block h-6 overflow-hidden bg-gray-300 rounded-full cursor-pointer">
                    </label>
                </div>
                <span className="font-medium text-gray-400">
                    Pink
                </span>
            </div> */

export default IndexPage
