// import {Link} from "gatsby"
import React, { useState, useEffect } from 'react';
import { Link } from "react-scroll";

const Navbar = () => {


  const [navbarOpen, setNavbarOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  // change state on scroll
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 99;
      if (isScrolled !== scrolled) {
        setScrolled(!scrolled);
      }

    };

    document.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      // clean up the event handler when the component unmounts
      document.removeEventListener('scroll', handleScroll);
    };
  }, [scrolled]);

  return (

    // h-24 sm:h-32

    <header className={scrolled ? "pt-12 pb-12 fixed flex items-center z-30 shadow-sm border-b-2 border-pink-500 w-full bg-white dark:bg-gray-900" : "flex pt-12 pb-12 items-center z-30 w-full dark:bg-gray-900"}>


      {navbarOpen ? (
        // <div className="w-screen" hidden={navbarOpen ? false : true}>


        <div className="container flex-col px-6 mx-auto" hidden={navbarOpen ? false : true} >


          <div className="container flex items-center justify-between mx-auto">
            <div className="text-3xl font-black text-gray-800 uppercase dark:text-white">
              <button href="#" className="font-extrabold uppercase focus:outline-none">
                <Link activeClass="active" to="home" spy={true} smooth={true} offset={-200} duration={500}>
                  <span className="text-pink-500">Soham</span>.Manoli</Link></button>
            </div>
            <div className="flex items-center">
              <nav className="items-center hidden text-lg text-gray-800 uppercase font-sen dark:text-white lg:flex">
              </nav>

              <button className="flex flex-row ml-4 focus:outline-none " onClick={() => setNavbarOpen(!navbarOpen)}>
                <span className="w-1 h-6 mb-1 ml-1 mr-1 bg-gray-800 dark:bg-white"></span>
                <span className="w-1 h-6 mb-1 mr-1 bg-gray-800 dark:bg-white"></span>
                <span className="w-1 h-6 mb-1 mr-1 bg-gray-800 dark:bg-white"></span>
              </button>
            </div>

          </div>


              <button href="#" className="flex px-6 py-2 mt-6 text-xl focus:outline-none dark:text-white">
                <Link activeClass="active" to="about" spy={true} smooth={true} offset={-200} duration={500}>
                  About Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>
          
              <button href="#" className="flex px-6 py-2 text-xl focus:outline-none dark:text-white">
                <Link activeClass="active" to="projects" spy={true} smooth={true} offset={-200} duration={500}>
                  Projects &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>

              <button href="#" className="flex px-6 py-2 text-xl focus:outline-none dark:text-white">
                <Link activeClass="active" to="skills" spy={true} smooth={true} offset={-120} duration={500}>
                  Skills &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>

              <button href="#" className="flex px-6 py-2 text-xl focus:outline-none dark:text-white">
                <Link activeClass="active" to="contact" spy={true} smooth={true} offset={-70} duration={500}>
                  Contact Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>


        </div>
      ) : (
        <div className="container flex items-center justify-between px-6 mx-auto" hidden={setNavbarOpen}>
            <div className="text-3xl font-black text-pink-500 uppercase transition duration-200 hover:text-indigo-500 hover:text-4xl">
            <button href="#" className="font-extrabold uppercase focus:outline-none">
              <Link activeClass="active" to="home" spy={true} smooth={true} offset={-200} duration={500}>
              Soham<span className="text-gray-800 dark:text-white">.Manoli</span></Link></button>
          </div>
          <div className="flex items-center">
            <nav className="items-center hidden text-lg text-gray-800 uppercase font-sen dark:text-white lg:flex">


              <button href="#" className="flex px-6 py-2 transition duration-200 rounded dark:shadow-md focus:outline-none hover:bg-pink-500 ">
                <Link activeClass="active" to="about" spy={true} smooth={true} offset={-200} duration={500}>
                  About Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link></button>
              {/* <a href="#about" className="flex px-6 py-2">
                   Awards &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a> */}
              <button href="#" className="flex px-6 py-2 transition duration-200 rounded dark:shadow-md focus:outline-none hover:bg-pink-500 ">
                <Link activeClass="active" to="projects" spy={true} smooth={true} offset={-200} duration={500}>
                  Projects &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>
              <button href="#" className="flex px-6 py-2 transition duration-200 rounded dark:shadow-md focus:outline-none hover:bg-pink-500 ">
                <Link activeClass="active" to="skills" spy={true} smooth={true} offset={-120} duration={500}>
                  Skills &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>
              <button href="#" className="flex px-6 py-2 transition duration-200 rounded dark:shadow-md focus:outline-none hover:bg-pink-500 ">
                <Link activeClass="active" to="contact" spy={true} smooth={true} offset={-70} duration={500}>
                  Contact Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </Link>
              </button>
            </nav>
            <button className="flex flex-col ml-4 lg:hidden focus:outline-none " onClick={() => setNavbarOpen(!navbarOpen)}>
              <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
              </span>
              <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
              </span>
              <span className="w-6 h-1 mb-1 bg-gray-800 dark:bg-white">
              </span>
            </button>

          </div>
        </div>
      )}

    </header>

  )
}

export default Navbar